import React, { PureComponent } from 'react'
import { withState } from 'recost';
import { translate } from 'react-multi-lang'
import { api } from 'services'
import TableFooter from './components/TableFooter'
import TableBody from './components/TableBody'
import TableHeader from './components/TableHeader'
import classnames from 'classnames'
import style from './style.module.scss'

const tableClasses = classnames(
  'table',
  style.fullWidthTable
)

class Table extends PureComponent {
  componentDidMount() {
    if (!this.props.tableConfig) {
      api.table.getTableConfig(this.props.match.params.tableName)
    }
    if (!this.props.tableData) {
      api.table.getTableData(this.props.match.params.tableName)
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.match.params.tableName !== prevProps.match.params.tableName) {
      if (!this.props.tableConfig) {
        api.table.getTableConfig(this.props.match.params.tableName)
      }
  
      if (!this.props.tableData) {
        api.table.getTableData(this.props.match.params.tableName)
      }
    }
  }

  render() {
    const {
      t,
      tableConfig,
      tableData,
    } = this.props

    return (
      <>
        <h1 className='title'>{t('table.title')} {tableConfig && tableConfig.name}</h1>
        {tableConfig && (
          <table className={tableClasses}>
            <TableHeader items={tableConfig.configuration}/>
            <TableBody tableName={tableConfig.name} data={tableData.data} configuration={tableConfig.configuration}/>
            <TableFooter items={tableConfig.configuration}/>
          </table>
        )}
      </>
    )
  }
}

const mapStateToProps = (state, props) => {
  return {
    tableConfig: state.tablesConfiguration[props.match.params.tableName],
    tableData: state.tablesData[props.match.params.tableName],
  }
}
export default translate(
  withState(mapStateToProps)(
    Table
  )
)
