import React, { memo } from 'react'
import { NavLink } from 'react-router-dom'
import { translate } from 'react-multi-lang'
import style from './style.module.scss'

export default memo(translate(({ data, configuration, tableName, t }) => {
  console.log(configuration, data)
  return (
    <tbody>
      {(configuration && configuration.length && data && data.length && data.map(
        (dataItem, index) => (
          <tr key={index}>
            {configuration.map(
              (config, index) => (
                <td key={index}>{dataItem[config.name]}</td>
              )
            )}
            <td className={style.actionColumn}>
              <div className='buttons is-right'>
                <NavLink to={`/table/${tableName}/${dataItem.id}`} className='button' activeClassName='is-active'>
                  <span class='icon is-small'>
                    <i class='fas fa-edit'></i>
                  </span>
                </NavLink>
                <button class='button is-danger is-outlined'>
                  <span class='icon is-small'>
                    <i class='far fa-trash-alt'></i>
                  </span>
                </button>
              </div>
            </td>
          </tr>
        )
      )) || null}
    </tbody>
  )
}))