import React, { memo } from 'react'

export default memo(({ items }) => (
  <thead>
    <tr>
      {(items && items.length && items.map(
        (config, index) => {
          if (config.visible && config.visible.main) {
            return <th key={index}><abbr title={config.name}>{config.verbose}</abbr></th>
          }
          return null
        }
      ).filter(a => a)) || null}
      <th/>
    </tr>
  </thead>
))